import { Injectable } from '@angular/core';

import { PositionElementSettings } from '../models/position-element-settings.interface';
import { Position } from '../models/position.interface';

import { DOMService } from './dom.service';

import { eitherRect } from '../util';

/**
 * @hidden
 */
@Injectable()
export class PositionService {
    constructor(private _dom: DOMService) { }

    public positionElement(settings: PositionElementSettings): Position {
        const {
            anchor,
            currentLocation,
            element,
            anchorAlign,
            elementAlign,
            collisions
        } = settings;

        const viewPort = settings.viewPort || this._dom.windowViewPort(element);
        const anchorRect = eitherRect(this._dom.offset(anchor), currentLocation);
        const tempRect = this._dom.offset(element);
        const elementRect = {
            height: tempRect.height,
            left: currentLocation.left,
            top: currentLocation.top,
            width: tempRect.width
        };

        const result = this._dom.restrictToView({
            anchorAlign,
            anchorRect,
            collisions,
            elementAlign,
            elementRect,
            viewPort
        }) as Position;

        return {
            flipped: result.flipped,
            offset: this._dom.addOffset(elementRect, result.offset)
        } as Position;
    }
}



